import React, { Component } from 'react';
import axios from 'axios';


class About extends Component {

  constructor() {
    super();
    this.state = {
      article_title: '',
      article_body: ''
    };
  }

  componentDidMount() {
    var self = this;
    this.serverRequest = axios.get('https://gcp1.newideas.tw/workspace/d8base-free-test/aboutus?_format=json')
      .then(function (result) {
        self.setState({
          article_title: result.data.title["0"].value,
          article_body: result.data.body["0"].value
        });
      })
  }

  render() {
    return (
      <div>

        <div className="row ">

          <div className="col">
            <h4>{this.state.article_title}</h4>

            <div className="card-block" dangerouslySetInnerHTML={{ __html: this.state.article_body }} />



          </div>
        </div>
      </div>
    );
  }
}

export default About



