import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import './index.css';



const wrapper = document.getElementById('root')
  ;

const baseURL = wrapper.getAttribute('data-drupal-url');
ReactDOM.render(
  <App baseURL={baseURL} />,
  wrapper
);



