import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import axios from 'axios';
import '../style/navbar.css';

class Navbar extends Component {

  constructor(props) {
    super(props);
    this.state = {
      username: localStorage.getItem('username'),
      newidea_img: ''
    };
  }

  componentWillReceiveProps() {
    this.setState({
      'username': localStorage.getItem('username')
    });
  }

  renderUserLinks() {
    var loggedIn = localStorage.getItem('auth');
    if (loggedIn) {
      return (
        <span>
          <NavLink className="dropdown-item" activeClassName="active" to="/user/profile">
            <i className="fa fa-user-circle"></i> Profile
          </NavLink>
          <NavLink className="dropdown-item" activeClassName="active" to="/user/logout">
            <i className="fa fa-sign-out"></i> Logout
          </NavLink>
        </span>
      );
    }
    else {
      return (
        <span>
          <NavLink className="dropdown-item" activeClassName="active" to="/user/register">
            <i className="fa fa-id-card"></i> Register
          </NavLink>
          <NavLink className="dropdown-item" activeClassName="active" to="/user/login">
            <i className="fa fa-sign-in"></i> Login
          </NavLink>
        </span>
      );
    }
  }





  componentDidMount() {

    this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/newideas-logo?_format=json')
      .then((result) => {
        console.log(result.data.field_media_image[0].url);
        this.setState({
          newidea_img: result.data.field_media_image[0].url
          //   article_title: result.data.title["0"].value,
          //   article_body: result.data.body["0"].value
        });
      })

  }


  render() {
    const logo = "https://image.flaticon.com/icons/svg/1245/1245156.svg";
    var userLinkTitle = this.state.username ? this.state.username : 'User';

    return (
      <div className="navbar_area">
        <div className="row mg_0">

          <nav className="navbar navbar-expand-md  col navbar-toggleable-md navbar-light bg-light">
            <NavLink exact className="nav-link" activeClassName="active" to="/index">
              <div>
                {/* <img className="navbar_imgA" src={this.state.newidea_img} alt="" /> */}
                <img className="navbar_imgA" src={logo} alt="" />
              </div>
            </NavLink>
            <div className="row">
              <div className="col test">
                <button className="navbar-toggler menu_button" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
                </button>
              </div>

              {/* justify-content-center align-items-center */}
              <NavLink exact className="nav-link" activeClassName="active" to="/index">
                <div className="col " >
                  <img className=" navbar_imgB" src={logo} alt="" ></img>
                </div>
              </NavLink>

            </div>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">

              <div className="navbar-nav navbar_center">
                <ul className=" navbar-nav mr-auto mt-2 mt-lg-0">

                  <li className="nav-item">
                    <NavLink exact className="nav-link" activeClassName="active" to="/about">
                      {/* <i className="fa fa-info-circle"></i> */}
                      
                  </NavLink>

                  </li>
                  <li className="nav-item dropdown">
                    <a className="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      
        </a>
                    <div className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                      <a className="dropdown-item" href="#">ipsum</a>
                      <a className="dropdown-item" href="#">dolor</a>
                      <a className="dropdown-item" href="#">amet</a>
                    </div>
                  </li>
                  <li className="nav-item">
                    <NavLink exact className="nav-link" activeClassName="active" to="/service">
                      {/* <i className="fa fa-newspaper-o"></i> */}
                      
                  </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink exact className="nav-link" activeClassName="active" to="/DigitalMarketing">
                      {/* <i className="fa fa-newspaper-o"></i> */}
                      
                  </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink exact className="nav-link" activeClassName="active" to="/technologyExchange">
                      {/* <i className="fa fa-newspaper-o"></i> */}
                      
                      </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" activeClassName="active" to="/contact">
                      {/* <i className="fa fa-envelope"></i>  */}
                      
                      </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" activeClassName="active" to="/Upload">

                      {/* <i className="fas fa-plus"></i> */}
                      
                  </NavLink>
                  </li>
                  <li className="nav-item navhide dropdown">
                    <a className="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" data-toggle="dropdown">
                      {/* <i className="fa fa-user"></i> */}
                      {userLinkTitle}
                    </a>
                    <div className="dropdown-menu">
                      {this.renderUserLinks()}
                    </div>
                  </li>


                </ul>

              </div>

            </div>

          </nav>
        </div>



      </div>


    );
  }
}

export default Navbar



