import React, { Component } from 'react';
import axios from 'axios';
import '../style/DigitalMarketing.css';

class service extends Component {

    constructor() {
        super();
        this.state = {
            DigitalMarketing_body: '',
            DigitalMarketing_title: '',
            contact_QRcode: '',
            data: ''
            // article_title: '',
            // article_body: ''
        };

    }



    componentDidMount() {

        this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/rest/api/basic-page?_format=json')
            .then((result) => {
                //id = 58.

                let result1 = result.data.filter(data => (data.nid[0].value == 57));
                this.setState({
                    DigitalMarketing_body: result1[0].body[0].value,
                    DigitalMarketing_title: result1[0].title[0].value
                    // contact_body: result.data.body[0].value,
                    // contact_QRcode: result.data.field_image[0].url
                });
            })
    }



    // <div dangerouslySetInnerHTML={{ __html: content }}></div>


    render() {

        //console.log(this.state.DigitalMarketing_body)

        return (
            <div className="col-12  DigitalMarketing">
                <div>
                    <h1>
                        
                        {/* {this.state.DigitalMarketing_title} */}

                    </h1>
                    {/* <div dangerouslySetInnerHTML={{ __html: this.state.DigitalMarketing_body }}>

                    </div> */}
                    {/* <img src={this.state.contact_QRcode} alt="" /> */}
                </div>
            </div>

        );
    }
}

export default service



