import React, { Component } from 'react';
import axios from 'axios';
import '../style/conntact.css';


class contact extends Component {

  constructor(props) {
    super(props);
    this.state = {
      contact_title: '',
      contact_body: '',
      contact_QRcode: ''
      // article_title: '',
      // article_body: ''
    };
  }

  componentDidMount() {
    if (this.props.baseURL) {
      console.log(this.props.baseURL + 'rewqrewqrweq')
    }
    this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/node/60?_format=json')
      .then((result) => {
        console.log(result.data.field_image[0].url);
        this.setState({
          contact_title: result.data.title[0].value,
          contact_body: result.data.body[0].value,
          contact_QRcode: result.data.field_image[0].url
        });
      })
  }



  // <div dangerouslySetInnerHTML={{ __html: content }}></div>


  render() {

    return (
      <div className="col-12 contact"  >
        <div>
          <h1>
            {this.state.contact_title}

          </h1>
          <div dangerouslySetInnerHTML={{ __html: this.state.contact_body }}>
            {/* <img src={this.state.contact_QRcode} alt="" /> */}
          </div>

          <img src={this.state.contact_QRcode} alt="" />
        </div>
      </div>

    );
  }
}

export default contact



