import React, { Component } from 'react';
import axios from 'axios';

import {
  NavLink
} from 'react-router-dom';

class Articles extends Component {

  constructor() {
    super();
    this.state = {
      articles: [],
      article_title: '',
      article_body: '',
      keyword: '',
      tags: [],
      tagnode: '',
      article_img: '',
      tagsName: ''
    };

    this.updateSearchKeyword = this.updateSearchKeyword.bind(this);
    this.updateSelectedArticle = this.updateSelectedArticle.bind(this);
    this.updateSelectedTags = this.updateSelectedTags.bind(this);


  }

  updateSearchKeyword(event) {
    this.setState({
      'keyword': event.target.value
    })
  }

  updateSelectedArticle(event) {
    this.fetchArticle(event.target.getAttribute('data-value'));
    console.log(event.target.getAttribute('data-value'))
  }

  updateSelectedTags(event) {
    console.log(event.target.getAttribute('value'))
    this.setState({
      'tagsName': event.target.getAttribute('value'),
      'tagnode': event.target.getAttribute('data-value')
    })

  }



  fetchArticle(nid) {
    var id;
    if (nid !== undefined) {
      id = nid;
    }
    else if (this.props.match.params.id !== undefined) {
      id = this.props.match.params.id;
    }
    else {
      id = 6;
    }
    var self = this;
    this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/node/' + id + '?_format=json')
      .then(function (result) {
        console.log(result.data)
        let body = "";
        if (result.data.body !== undefined) {
          body = result.data.body["0"].value;
        }


        let img;
        if (result.data.field_image[0]) {
          img = result.data.field_image[0].url;

        }


        self.setState({
          article_title: result.data.title["0"].value,
          article_body: body.replace('/sites/default/files', 'http://gcp1.newideas.tw/workspace/d8base-free-test/sites/default/files'),
          article_img: img
        });
      })
  }

  fetchArticleTitles() {
    var self = this;
    this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/restful/articles?_format=json')
      .then(function (result) {


        self.setState({
          articles: result.data
        });
      })
  }

  // http://gcp1.newideas.tw/workspace/d8base-free-test/taxonomy/term/1

  fetchArticleTags() {
    var self = this;
    let tagsNumber = 1;
    this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/api/tags?_format=json')
      .then(function (result) {
        self.setState({
          tags: result.data
        });

      })
  }





  componentDidMount() {
    this.fetchArticleTags();
    this.fetchArticleTitles();
    this.fetchArticle();
  }

  render() {
    var rows = [];
    let tagsName = this.state.tagsName;
    var dropdownrows = [];
    var self = this;
    let tagsnode = this.state.tagnode;

    this.state.tags.forEach(function (article, index) {

      if (article.name[0].value.toLowerCase().indexOf(self.state.keyword.toLowerCase()) !== -1) {
        dropdownrows.push(<a data-value={article.tid[0].value} key={article.tid[0].value} value={article.name[0].value} onClick={self.updateSelectedTags} className="dropdown-item" href="#">{article.name[0].value}</a>);
      }
    });

    this.state.articles.forEach(function (article, index) {
      if (article.title[0].value.toLowerCase().indexOf(self.state.keyword.toLowerCase()) !== -1 && !tagsnode) {
        var path = '/articles/' + article.nid[0].value;
        rows.push(<NavLink key={article.nid[0].value} data-value={article.nid[0].value} onClick={self.updateSelectedArticle} to={path} className="list-group-item list-group-item-action">{article.title[0].value}</NavLink>);

      } else if (article.title[0].value.toLowerCase().indexOf(self.state.keyword.toLowerCase()) !== -1 && article.field_tags
        && article.field_tags[0].url == '/workspace/d8base-free-test/taxonomy/term/' + tagsnode) {

        var path = '/articles/' + article.nid[0].value;
        rows.push(<NavLink key={article.nid[0].value} data-value={article.nid[0].value} onClick={self.updateSelectedArticle} to={path} className="list-group-item list-group-item-action">{article.title[0].value}</NavLink>);
      }
    })

    return (
      <div className="row top-buffer" >
        <div className="col-md-4">
          <form>
            <div className="form-group">
              <input name="keyword" value={this.state.keyword} onChange={this.updateSearchKeyword} type="text" className="form-control" placeholder="Search articles" />
            </div>
          </form>

          <div className="dropdown">
            <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              {tagsName}
            </button>
            <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
              <a onClick={self.updateSelectedTags} value="" className="dropdown-item" href="#"></a>
              {dropdownrows}
            </div>
          </div>

          <br />

          <div className="list-group">
            {rows}
          </div><br />
        </div>
        <div className="col-md-8">
          <div className="card text-center">
            <div className="card-header">
              {this.state.article_title}
            </div>
            {this.state.article_body && <div className="card-block" dangerouslySetInnerHTML={{ __html: this.state.article_body }} />}
            {this.state.article_img && <img className="img-responsive" width="100%" height="80%" src={this.state.article_img} />}


            <div className="card-footer text-muted text-left">
              <em><small>By Goran Nikolovski.</small></em>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Articles



