import React, { Component } from 'react';
import '../style/sliderindex.css';

class Slider1 extends Component {

    constructor() {
        super();
        this.state = {
            selectNumber: '',
            now_index: 0,
            span: 1100,
            left: 0,
            data: [
                {
                    title: "",
                    description: "",
                    cover: "https://picsum.photos/400/300?image=1084"
                },
                {
                    title: "",
                    description: "",
                    cover: "https://unsplash.it/1140/800?image=1083"
                },
                {
                    title: "",
                    description: "",
                    cover: "https://unsplash.it/1140/800?image=1082"
                },
                {
                    title: "",
                    description: "",
                    cover: "https://unsplash.it/1140/800?image=1081"
                },
                {
                    title: "",
                    description: "",
                    cover: "https://unsplash.it/1140/800?image=1080"
                },
                {
                    title: "",
                    description: "",
                    cover: "https://unsplash.it/1140/800?image=1079"
                }
            ]
        }
        this.selectSlider = this.selectSlider.bind(this);
        this.bg_css = this.bg_css.bind(this);
        this.selectSliderRight = this.selectSliderRight.bind(this);
        this.selectSliderLeft = this.selectSliderLeft.bind(this);


    }

    selectSlider(num) {
        let datakLength = this.state.data.length;
        let new_index = (this.state.now_index + num + datakLength) % datakLength
        let newLeft = -this.state.span * new_index;
        this.setState({
            left: newLeft,
            now_index: new_index
        })
    }
    selectSliderRight() {
        let num = 1;
        this.selectSlider(num);
    }
    selectSliderLeft() {
        let num = -1;
        this.selectSlider(num);
    }



    bg_css(url) {
        return {
            'backgroundImage': `url(${url})`
        }
    }


    // className = { 'box-color'this.state.selectSlider }
    render() {
        const listItems = this.state.data.map((number, id) =>
            <div key={id} className={`postbox  ${this.state.now_index == id ? "cur_item" : null}`}>
                <div className="cover" style={this.bg_css(number.cover)}>
                    <div className="infos">
                        <h1>
                            {number.title}
                        </h1>
                        <h5>
                            {number.description}
                        </h5>
                    </div>
                </div>
            </div>
        );

        return (
            <div>

                <div className="app">

                    <div className="postarea">
                        <div className='posts' style={{ left: `${this.state.left}` }}  >
                            {listItems}
                        </div>
                    </div>
                    <div onClick={this.selectSliderLeft} className="control_left">
                        <div className="fa fa-angle-left"></div>
                    </div>

                    <div onClick={this.selectSliderRight} className="control_right">
                        <div className="fa fa-angle-right"></div>

                    </div>

                </div>
            </div>
        );
    }
}

export default Slider1



