import React, { Component } from 'react';
import axios from 'axios';

class Contact extends Component {

    constructor(props) {
        super(props);
        this.initAjax();

        this.state = {
            articlename: '',
            articleMail: '',
            articlePhone: '',
            articleTitle: '',
            file: null,
            subject: '',
            message: '',
            success: '',
            error: ''
        };

        this.setName = this.setName.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.setMassage = this.setMassage.bind(this);
        this.fileChange = this.fileChange.bind(this);
        this.setMail = this.setMail.bind(this);
        this.setTitle = this.setTitle.bind(this);
        this.setPhone = this.setPhone.bind(this);



    }

    initAjax() {
        // regex to strip the first subdomain and trailing path
        // http://sub.domain.com/some/path.html -> http://domain.com/
        let baseURL = 'https://gcp1.newideas.tw/workspace/d8base-free-test';

        const tokenURL = baseURL + '/rest/session/token';
        // first request is to fetch csrf token

        const req = axios.get(tokenURL, {
            withCredentials: true // required to send auth cookie
        });
        req.then((response) => {
            const csrf_token = response.data;
            this.ajax = axios.create({
                baseURL,
                withCredentials: true, // include auth cookie
                headers: {
                    'X-CSRF-Token': csrf_token,
                    'Content-Type': 'application/hal+json',
                    'Accept': 'application/hal+json'
                }
            });
            // set baseURL as property on 'this'
            this.baseURL = baseURL;

        });
    }





    fileChange(e) {
        this.setState({ file: e.target.files[0] })
        console.log(e.target)

    }
    setName(event) {
        this.setState({
            'articlename': event.target.value
        })
    }
    setMail(event) {
        this.setState({
            'articleMail': event.target.value
        })
    }

    setTitle(event) {
        this.setState({
            'articleTitle': event.target.value
        })
    }
    setPhone(event) {
        this.setState({
            'articlePhone': event.target.value
        })
    }

    setMassage(event) {
        this.setState({
            'message': event.target.value
        })
    }
    handleSubmit(event) {


        console.log(this.state.file)
        console.log(this.state.name)

        let node = {
            "_links": {
                "type": {
                    "href": "http://gcp1.newideas.tw/workspace/d8base-free-test/rest/type/node/article"
                }
            },
            "type": {
                "target_id": "article"
            },
            "title": {
                "value": this.state.articlename
            },
            "body": {
                "value": this.state.message
            }

        }
        let nodeimg = {
            "_links":
            {
                "type": "http://gcp1.newideas.tw/workspace/d8base-free-test/file/file"
            },
            "filename": [{ "value": "input.jpg" }],
            "filemime": [{ "value": "image/jpeg" }],
            "data": [{ "value": "insert-output-from-base64-here" }]
        }
        var self = this;
        let req;
        req = this.ajax.post(`/entity/node?_format=hal_json`, node)


            .then(function (response) {
                self.setState({
                    'articlename': '',
                    'message': ''
                });
                event.preventDefault();
            })
            .catch(function (error) {
                var errorResponse = error.response.data.message;
                errorResponse = errorResponse.replace(/(?:\r\n|\r|\n)/g, '<br />');

                self.setState({
                    'success': '',
                    'error': errorResponse
                });
            });
    }
    render() {
        return (
            <div className="row top-buffer">
                <div className="col">
                    <form className="col-md-6 offset-md-3 text-center" onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <input name="name" value={this.state.articlename} onChange={this.setName} required type="textfield" className="form-control" placeholder="" />
                        </div>

                        <div className="form-group">
                            <input type="email" value={this.state.articleMail} onChange={this.setMail} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email"></input>
                        </div>
                        <div className="form-group">
                            <input type="email" name="name" value={this.state.articlePhone} onChange={this.setPhone} required type="textfield" className="form-control" placeholder="Phone" />
                        </div>
                        <div className="form-group">
                            <input name="name" value={this.state.articleTitle} onChange={this.setTitle} required type="textfield" className="form-control" placeholder="" />
                        </div>
                        {/* <div className="form-group">
                            <input name="email" value={this.state.email} onChange={this.handleChange} required type="email" className="form-control" placeholder="Enter email" />
                        </div> */}
                        {/* <div className="form-group">
                            <input name="subject" value={this.state.subject} onChange={this.handleChange} required type="textfield" className="form-control" placeholder="Enter subject" />
                        </div> */}
                        <div className="form-group">
                            <textarea name="message" value={this.state.messetStatesage} onChange={this.setMassage} required className="form-control" rows="5" placeholder=""></textarea>
                        </div>

                        <button type="submit" className="btn btn-primary"></button>
                        <div className="form-group messages">
                            <p className="success">{this.state.success}</p>
                            <p className="error" dangerouslySetInnerHTML={{ __html: this.state.error }} />
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default Contact



