import React, { Component } from 'react';
import axios from 'axios';
import '../style/TechnologyExchange.css';
import List from './List';

class TechnologyExchange extends Component {

    constructor() {
        super();
        this.state = {
            body: '',

            // article_title: '',
            // article_body: ''
        };
    }

    componentDidMount() {

        this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/rest/api/basic-page?_format=json')
            .then((result) => {
                //id=58.
                let result1 = result.data.filter(data => (data.nid[0].value == 59));
                this.setState({
                    body: result1[0].body[0].value,
                    title: result1[0].title[0].value
                    // contact_body: result.data.body[0].value,
                    // contact_QRcode: result.data.field_image[0].url
                });
            })



    }



    // <div dangerouslySetInnerHTML={{ __html: content }}></div>


    render() {


        return (
            <div>

                <div className="col TechnologyExchange">

                    <div className="area_center">
                        <div className="Technology_header">
                            <h1>{this.state.title}</h1>
                        </div>
                        <div className="Technology_body">
                            <div dangerouslySetInnerHTML={{ __html: this.state.body }}>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default TechnologyExchange



