import React, { Component } from 'react';
import axios from 'axios';
import '../style/Introduction.css';


class Introduction extends Component {

    constructor() {
        super();
        this.state = {
            field_tags: '',
            data: [],
            url: '',
            title: '',
            productModals: '',
            productModal: '',
            productModalsTitles: '',
            productModalsTitle: '',
            modalsImgAry: [],
            modalsImgs: []
        };


        this.fetchArticleData = this.fetchArticleData.bind(this);
        this.showProductModal = this.showProductModal.bind(this);
    }



    showProductModal = (e, id) => {
        this.setState({
            productModalsTitle: this.state.productModalsTitles[id],
            productModal: this.state.productModals[id].data4,
            modalsImgs: this.state.modalsImgAry[id]
        })

    }








    fetchArticleData() {
        this.serverRequest = axios.get('http://gcp1.newideas.tw/workspace/d8base-free-test/rest/api/newideas-portfolio?_format=json')
            .then((result) => {


                let modalsImgArys = [];
                let ref = result.data.forEach(data => {
                    let modalsImgAry = [];

                    let ref1 = data.field_image.forEach(data => {
                        let url = data.url;
                        let alt = data.alt;
                        let imgobj = { url, alt };
                        modalsImgAry.push(imgobj);
                    })
                    let modalsImgAry1 = modalsImgAry.slice(1);
                    modalsImgArys.push(modalsImgAry1);

                })


                let titles = [];
                let productModals = result.data.map(data => {
                    titles.push(data.title[0].value);
                    let data1 = data.body[0].value;
                    let data2 = data1.split('\n');
                    let data3 = data2[0].length;
                    let data4 = data1.slice(data3);

                    return { data4, titles }
                })



                this.setState({
                    productModals: productModals,
                    productModalsTitles: titles,
                    modalsImgAry: modalsImgArys
                });




                const Introduction1 = result.data.map(data => {

                    let img = data.field_image[0].url;
                    let data1 = data.body[0].value;
                    let data2 = data1.split('\n');
                    let data3 = data2[0];
                    return { data3, img };
                })
                this.setState({
                    data: Introduction1,
                });


                this.serverRequest = axios.get(`http://gcp1.newideas.tw${result.data[0].field_tags[0].url}?_format=json`)
                    .then((result1) => {
                        this.setState({
                            title: result1.data.name[0].value
                        })

                    })

            })
    }




    componentDidMount() {
        this.fetchArticleData();
    }
    /*
        field_tags: Array(1)
    0:
    target_id: 6
    target_type: "taxonomy_term"
    target_uuid: "a363ecc7-bc87-4181-b329-fe0010204a0d"
    url: "/workspace/d8base-free-test/taxonomy/term/6"
    __proto__: Object
    */



    render() {
        console.log(this.state.modalsImgAry)
        const dropdownrows = [];
        const self = this;
        const showmodal = [];

        this.state.modalsImgs.forEach((data, index) => {
            showmodal.push(
                <div className="modalsImgs">
                    <h5>{data.alt}</h5>
                    <img src={data.url} alt="" />
                </div>
            )

        })

        this.state.data.forEach(function (article, index) {

            dropdownrows.push(
                <div className="col-xl-4 col-lg-6 col-md-6" key={index}>
                    <div className="Introduction_post" >
                        <img src={article.img} onClick={(e, id) => self.showProductModal(e, id = index)} className="Introduction_cover" data-toggle="modal" data-target="#exampleModal" alt=" " />
                    </div>
                    <div className="Introduction_infos" dangerouslySetInnerHTML={{ __html: article.data3 }}>
                    </div>
                </div>
            )
        });




        return (
            <div className="area">
                <div className="title_area">
                    <div className="h3"> {this.state.title}</div>
                    <em>test</em>
                </div>

                <div className="row dropdownrows">
                    {dropdownrows}
                </div>










                <div className="modal fade" id="exampleModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">{this.state.productModalsTitle}</h5>
                                {/* <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button> */}
                            </div>
                            <div className="modal-body">
                                <div className="modal-text">
                                    <div dangerouslySetInnerHTML={{ __html: this.state.productModal }}>
                                    </div>
                                </div>
                                {showmodal}
                                <div className="modalbtn">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal"></button>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>



            </div>

        );
    }
}

export default Introduction



