import React, { Component } from 'react';

import axios from 'axios';
import {
  BrowserRouter as Router,
  Route,
  browserHistory,
  HashRouter
} from 'react-router-dom';
import Introduction from './components/Introduction.jsx';
import Navbar from './components/navbar.jsx';
import Slider1 from './components/slider1.jsx';
import Footer from './components/footer.jsx';
import Contactcard from './components/contactcard.jsx';
import Home from './components/home.jsx';
import Articles from './components/articles.jsx';
import Service from './components/service.jsx';
import About from './components/about.jsx';
import Contact from './components/contact.jsx';
import Register from './components/register.jsx';
import TechnologyExchange from './components/TechnologyExchange.jsx';
import Login from './components/login.jsx';
import Profile from './components/profile.jsx';
import Logout from './components/logout.jsx';
import Upload from './components/Upload.jsx';
import DigitalMarketing from './components/DigitalMarketing.jsx';
import index from './components/index.jsx';
import List from './components/List.jsx';


class App extends Component {

  constructor(props) {
    super(props);
    // this.initAjax();

  }


  // initAjax() {
  //   // regex to strip the first subdomain and trailing path
  //   // http://sub.domain.com/some/path.html -> http://domain.com/
  //   let baseURL;
  //   if (this.props.baseURL) {
  //     baseURL = this.props.baseURL;
  //   } else {
  //     const regex = /(.*:\/\/)[^\.]*\.([^\/]*).*$/;
  //     baseURL = window.location.href.replace(regex, '$1$2');
  //     // https://gcp1.newideas.tw/workspace/d8base-free-test/rest/session/token
  //   }


  //   this.baseURL = baseURL;
  // }




  render() {


    return (
      <HashRouter>
        <Router history={browserHistory}>
          <div className="container-fluid">
            <Navbar />
            <div className="row">

              {/* <Slider1 />*/}
              <div className="col-12">
                <Route path="/index" component={index} />
              </div>
              <div className="col-3">
                <List />
              </div>

              <div className="col-9">

                <Route path="/about" component={About} />
                <Route path="/contact" component={Contact} />
                <Route exact path="/about" component={Home} />
                <Route path="/DigitalMarketing" component={DigitalMarketing} />
                <Route path="/articles/:id?" component={Articles} />
                <Route path="/service" component={Service} />
                <Route path="/technologyExchange" component={TechnologyExchange} />
                <Route path="/Introduction" component={Introduction} />
                <Route path="/user/register" component={Register} />
                <Route path="/user/login" component={Login} />
                <Route path="/user/profile" component={Profile} />
                <Route path="/user/logout" component={Logout} />
                <Route path="/Upload" component={Upload} />
              </div>

            </div>
            <Footer />
          </div>
        </Router>
      </HashRouter>
    );
  }
}




export default App



